@import "syntax";
@import "rebox";

$content-width: 650px;

@mixin content-width($width: $content-width) {
  max-width: $content-width;
  margin: {
    left: auto;
    right: auto; } }

html {
  font-size: 18px;
  @media screen and (max-device-width: 500px) {
    font-size: 24px; } }

body {
  margin: 0;
  width: 100%;
  padding: 1rem 0 200px;
  font-family: "Avenir Next", "Helvetica Neue", "Helvetica", sans-serif;
  line-height: 1.5;

  &>header, &>article,
  &>h1, &>h2, &>h3, &>h4, &>h5, &>h6,
  &>p, &>blockquote, &>code, &>figure,
  &>ul, &>ol, &>table,
  &>.footnotes,
  &>.highlighter-rouge, &>.gist, {
    @include content-width;
    box-sizing: border-box; } }

figure {
  img {
    display: block;
    margin: auto {
      left: auto;
      right: auto; } } }

blockquote,
figcaption {
  font-family: "Georgia", serif;
  color: #666;
  font-size: 0.9rem; }

blockquote {
  border-left: 6px solid #ddd;
  padding-left: 1.5rem; }

figcaption {
  font-style: italic;
  text-align: center;
  padding: 0.6rem 0 0.8rem; }

sup {
  font-size: 0.83em;
  vertical-align: super;
  line-height: 0; }

a:link {
  color: #295091; }

a:visited {
  color: inherit; }

table {
  border-collapse: collapse; }
th, td {
  border: 1px solid #ccc;
  padding: 0.4em 0.6em; }

code {
  background: #f7f1f3;
  color: #4b4c4d;
  font-family: "SFMono-Regular", monaco, monospace;
  font-size: 85%; }

pre > code {
  display: block;
  background: #f6f8f5; }

h1, h2, h3, a, table {
  code {
    background: none;
    color: inherit; } }

.intro {
  font-family: "Georgia", serif;
  font-style: italic;
  color: #666; }

.footnotes {
  margin-top: 4em;
  padding-top: 1em;
  border-top: 1px solid #ccc;
  font-size: 0.9em; }

body > .full-width {
  display: block;
  max-width: 100%;
  margin: 0;
  padding: {
    top: 1rem;
    bottom: 1rem; } }

.screenshot {
  img {
    box-shadow: 0 0 5px 3px #eee; }
  &.full-width {
    img {
      width: 100%; } } }

.twil-year {
  padding-top: 1em; }

.twil-archive {
  padding-left: 0;
  list-style: none;
  li {
    padding-bottom: .5em;
    p {
      vertical-align: bottom;
      margin: 0;
      font-size: 0.8em;
      code {
        background: none; } }
    .tag {
      vertical-align: top;
      font-size: 80%;
      font-variant: small-caps;
      color: #999;
      padding-right: 7px; } } }

.twil {
  padding-top: 1.5rem;
  &>header {
    text-align: center;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    &>h1 {
      color: #999;
      font-weight: 300;
      font-size: 36px;
      margin: 0;
      a {
        text-decoration: none; } }
    &>p {
      color: #999;
      font-size: 14px;
      margin: 0; } }

  h2 {
    font-variant: small-caps;
    font-size: 16px;
    font-weight: normal;
    text-align: left;
    color: #999;
    span.pubdate {
      float: right; } }

  img {
    display: block;
    margin: 0 auto; } }

body>.pubdate {
  text-align: right;
  font-size: 80%;
  color: #ccc; }

.image-collection {
  display: table;
  ul {
    display: table-row; }
  li {
    display: table-cell; }
  img {
    width: 100%;
    cursor: zoom-in; }
  figcaption {
    @include content-width; } }

.home-link {
  font-size: 0.95rem;
  a, a:link, a:visited {
    color: #777; } }
